import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location } from '@angular/common';
import 'rxjs/add/operator/switchMap';
import { Router, NavigationEnd } from '@angular/router';

import {LicService} from '../services/lic/lic.service';
import {LicPolicy} from '../services/lic/lic';
@Component({
    selector: 'app-policydetail',
    templateUrl: './policy-detail.component.html',
    styleUrls: ['./policy-detail.component.css']
})

export class PolicyDetailComponent implements OnInit {
    @Input() poli: LicPolicy;
    constructor(
        private licservice: LicService,
        private route: ActivatedRoute,
        private location: Location,
        private router: Router
    ) { }
    ngOnInit(): void {
        this.getlicselected();
        window.scrollTo(0, 0);
    }
    getlicselected(): void {
        this.route.paramMap
        .switchMap((params: ParamMap) =>
     this.licservice.getpolicy(+params.get('Mid')))
     .subscribe(poli => this.poli = poli);
    }
    goBack(): void{
        this.location.back();
    }

}



