import {Injectable} from '@angular/core';
import {ICICI, IciciPolicy} from './icici';
import {Policies, PoliciesD} from './icici-mock';

@Injectable()

export class ICICIService {


    getpolicies(): Promise<ICICI[]> {
        return Promise.resolve(Policies);
    }
    getpoliciesD(): Promise<IciciPolicy[]> {
        return Promise.resolve(PoliciesD);
    }
    getpolicy(id: number): Promise<IciciPolicy> {
        return this.getpoliciesD()
        .then(policies => policies.find(policy => policy.Mid === id));
    }
    // getpolicy(id: number): Promise<LIC[]>{
    //     return this.getpolicies().then(policies => policies.find(policy =>policy.id===id));
    // }
}



