import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {MaterialModule} from './material.module';
import {RouterModule} from '@angular/router';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';

import { AppComponent } from './app.component';
import {HomeComponent} from './Home/home.component';
import {AboutComponent} from './About/about.component';
import {PolicyDetailComponent} from './Policy-Detail/policy-detail.component';
import {PageNotFoundComponent} from './Page-No-Found/page-not-found.component';
import {ContactComponent} from './Contact/contact.component';
import {ClientComponent} from './Client/client.component';
import {PolicyDetailIcComponent} from './Policy-Detail/policy-detail-ic.component';
import {CarouselComponent} from './carousel/carousel.component';

import {LicService} from './services/lic/lic.service';
import {ICICIService} from './services/icici/icici.service';
import {MailService} from './services/mail.service';
import { NgStringPipesModule } from 'angular-pipes';

import {FlexLayoutModule} from '@angular/flex-layout';
import { MapComponent } from './map/map.component';
@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    AboutComponent,
    PolicyDetailComponent,
    PageNotFoundComponent,
    ContactComponent,
    ClientComponent,
    PolicyDetailIcComponent,
    CarouselComponent,
    MapComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    BrowserAnimationsModule,
    MaterialModule,
    RouterModule.forRoot([
      {
        path: '',
        component : MapComponent
      },
      {
        path: 'Ldetail/:Mid',
        component: PolicyDetailComponent
      },
      {
        path: 'Idetail/:Mid',
        component: PolicyDetailIcComponent
      },
      {
        path: '**',
        component: PageNotFoundComponent
      }
    ]),
    FlexLayoutModule,
    NgbModule.forRoot(),
    ReactiveFormsModule,
    NgStringPipesModule
  ],
  providers: [LicService, ICICIService, MailService],
  bootstrap: [AppComponent]
})
export class AppModule { }



