import { Component, Input, OnInit } from '@angular/core';
import { ActivatedRoute, ParamMap } from '@angular/router';
import { Location } from '@angular/common';
import 'rxjs/add/operator/switchMap';
import { NewlinesPipe } from 'angular-pipes/src/string/newlines.pipe';
import { Router, NavigationEnd } from '@angular/router';

import {IciciPolicy} from '../services/icici/icici';
import {ICICIService} from '../services/icici/icici.service';
import { Route } from '@angular/router/src/config';
@Component({
    selector: 'app-policydetail',
    templateUrl: './policy-detail.component.html',
    styleUrls: ['./policy-detail.component.css']
})

export class PolicyDetailIcComponent implements OnInit {
    @Input() poli: IciciPolicy;


    constructor(
        private iciciservice: ICICIService,
        private route: ActivatedRoute,
        private location: Location,
        private router: Router
    ) { }
    ngOnInit(): void {
        this.geticiciselected();
        window.scrollTo(0, 0);
    }

    geticiciselected(): void {
            this.route.paramMap
            .switchMap((params: ParamMap) =>
         this.iciciservice.getpolicy(+params.get('Mid')))
         .subscribe(poli => this.poli = poli);
    }
    goBack(): void{
        this.location.back();
    }
}



