import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {LicService} from '../services/lic/lic.service';
import {LIC, LicPolicy} from '../services/lic/lic';
import {ICICI, IciciPolicy} from '../services/icici/icici';
import {ICICIService} from '../services/icici/icici.service';
import {MediaChange, ObservableMedia} from '@angular/flex-layout';
import {Subscription} from 'rxjs/Subscription';
import {trigger, state, style, transition, animate, keyframes} from '@angular/animations';

@Component({
    selector: 'app-home',
    templateUrl: './home.component.html',
    styleUrls: ['./home.component.css']
})

export class HomeComponent implements OnInit {

    tilelic = [
        {text: 'LIC', cols: 1, rows: 1, color: 'yellow'},
    ];
    tileicici = [
        {text: 'ICICI Lombard', cols: 1, rows: 1, color: 'blue'},
    ];
    nbCols: number = 1;
    selectedpolicy: LicPolicy;
    policies: LIC[];
    policiesD: LicPolicy[];
    picici: ICICI[];
    piciciD: IciciPolicy[];

    constructor(
        private licservice: LicService,
        private iciciservice: ICICIService,
        public media: ObservableMedia,
        private router: Router
    ) {}
    updategrid(): void {
       this.media.subscribe( (change: MediaChange) => {
             this.nbCols = (this.media.isActive('xs')) ? 1 : 2;

        });
    }

    getPolicies(): void {
        this.licservice.getpolicies().then(policies => this.policies = policies);
    }
    getPolciesD(): void {
        this.licservice.getpoliciesD().then(policiesD => this.policiesD = policiesD);
    }

    getPIcici(): void {
        this.iciciservice.getpolicies().then(picici => this.picici = picici);
    }
    getPIciciD(): void {
        this.iciciservice.getpoliciesD().then(piciciD => this.piciciD = piciciD);
    }
    // onSelect(policy: LicPolicy): void{
    //     this.selectedpolicy = policy;
    //     this.router.navigate(['/detail', this.selectedpolicy.Mid]);
    // }

    ngOnInit(): void {
        this.updategrid();
        this.getPolicies();
        this.getPolciesD();
        this.getPIcici();
        this.getPIciciD();
    }
}



