import {Injectable} from '@angular/core';
import {LIC} from './lic';
import {Policies} from './lic-mock';
import {LicPolicy} from './lic';
import {PoliciesD} from './lic-mock';
@Injectable()

export class LicService {


    getpolicies(): Promise<LIC[]> {
        return Promise.resolve(Policies);
    }
    getpoliciesD(): Promise<LicPolicy[]> {
        return Promise.resolve(PoliciesD);
    }
    getpolicy(id: number): Promise<LicPolicy> {
        return this.getpoliciesD()
        .then(policies => policies.find(policy => policy.Mid === id));
    }
}



